
<template>
  <div id="index">

    <div class="navLeft">
      <div class="scroll">
        <ul>
          <!-- <div>{{$route.params.index}}</div> -->
          <li v-for="(item,key) in navItem" v-bind:class="{ active: key==$route.params.index }"><router-link :to="item.oid">{{item.text}}</router-link></li>
        </ul>
      </div>
    </div>

    <!-- <div class="indexRight">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div> -->
    <div class="indexRight">
      <loading></loading>
      <transition name="slide-down">
        <keep-alive>
              <router-view></router-view>
            </keep-alive>
      </transition>
    </div>

  </div>
</template>
<script>
  export default{
    name:'index',
    data(){
      return {
        navItem:[
          {text:"新品",oid:"/index/0"},
          {text:"豪车",oid:"/index/1"},
          {text:"奢品",oid:"/index/2"},
          {text:"美妆",oid:"/index/3"},
          {text:"美妆",oid:"/index/4"},
          {text:"美妆",oid:"/index/5"},
          {text:"美妆",oid:"/index/6"},
          {text:"美妆",oid:"/index/7"},
          {text:"美妆",oid:"/index/8"},
          {text:"美妆",oid:"/index/9"},
          {text:"美妆",oid:"/index/10"},
          {text:"美妆",oid:"/index/11"},
          {text:"美妆",oid:"/index/12"},
          {text:"美妆",oid:"/index/13"},
          {text:"美妆",oid:"/index/14"},
          {text:"美妆",oid:"/index/15"},
          {text:"美妆",oid:"/index/16"},
          {text:"美妆",oid:"/index/17"},
          {text:"美妆",oid:"/index/18"},
          {text:"美妆",oid:"/index/19"},
          {text:"美妆",oid:"/index/20"},
          {text:"美妆",oid:"/index/21"},
          {text:"美妆",oid:"/index/22"},
          {text:"美妆",oid:"/index/23"},
          {text:"美妆",oid:"/index/24"},
        ]
      }
    }
  }
</script>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  /*margin-top: 60px;*/
}
.navLeft {
  width: 20%;
  position: fixed;
  top:0;
  left: 0;
  bottom: 0;
  background: #f2f3f6;
  line-height: 46px;
  text-align: center;
}
.navLeft .scroll{
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}
.navLeft ul li{
  border-bottom: 1px solid #e4e5ea;
}
.navLeft ul li a{
  color: #666;
}
.navLeft ul li.active{
  background-color: #fff;
}
.navLeft ul li.active a{
  color: #111;
}
.indexRight{
  position: absolute;
  top:0;
  right: 0;
  left:20%;
  /*bottom: 0;*/
  overflow: hidden;
}
</style>


